.project .box {
  position: relative;
  display: flex;
  flex-direction: column;
  /* width: 300px;
  height: 300px;
  margin: 10px; */
  box-sizing: border-box;
  align-items: flex-start;
}

.project .box .imgBox{
  position: relative;
  overflow: hidden;
}

.project .box .imgBox img {
  max-width: 100%;
  transition: transform 2s;
}

.project .box:hover .imgBox img {
  transform: scale(1.2);
}

.project .box:hover .details {
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  background: rgba(0, 0, 0, 0.8);
}

.project .box:hover .details .content {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  padding: 15px;
  color: #fff;
  margin: 1em 1em 1em 2em;
}

.App {
  text-align: center;
}

.new-line {
  display: inline;
}

.nav-bar {

}

ul {
  display: inline-block;
  margin: -4% 0;
}

a.link:hover {
  text-decoration: underline;
}

.App-header {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: black;
  top:4em;
}

.heading-background{
  position: fixed;
  opacity: 0.05;
  font-size: 25em;
  left:-0.2em;
  top:-0.5em;
  z-index: -100;
  color:#9352ae;
}
header{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
header h1{
font-size: 1em;
font-weight: 400;
padding: 2em;
margin-right: 50em;
margin-top: -3em;
}
nav{
  position:fixed;
  left:60em;
  top:6em;
z-index: 100;
}
nav li{
  display: inline-block;
  cursor: pointer;

}
nav li .link{
  text-decoration: none;
  color: black;
  padding:1em;
  opacity: 0.5;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}
li .link:hover{
  opacity: 1;
}
.active{
  color:black;
  opacity: 1;
}

.header-title button{
  background: black;
  border: 0px;
  padding: 1em 3em;
  transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
}
.header-title button:hover{
  opacity: 0.8;
}
.header-title button a{
  font-family: 'Open Sans', sans-serif;
  color:white;
  text-decoration: none;
}
.header-title{
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-size: 3em;
  text-align: left;
  top:2em;
}
.about{
  display: flex;
  flex-direction: row;
}
.about-content{
  position: relative;
  left:15em;
}
.about-content h1{
  font-family: 'Montserrat', sans-serif;
  font-size: 5em;
  text-align: left;
  padding-bottom: 0.3em;
}
.about-content p{
  font-size: 1.3em;
  text-align: left;
  width: 37em;
  padding-bottom: 1.3em;
}

.about img{
  margin-left: 5em;
}
.work{
  position: relative;
  display: flex;
  flex-direction: column;
  left:15em;
  top:8em;
  align-items: flex-start;
}
.work .heading{
  font-family: 'Montserrat', sans-serif;
  font-size: 5em;
  text-align: left;
}
.work .work-content{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  grid-gap: 2em;
}

.project img{
width: 30em;
height: 30em;
}
.contact{
  position: relative;
  top:20em;
  left:15em;
  text-align: left;
}
.contact h1{
    font-family: 'Montserrat', sans-serif;
  font-size: 5em;
  text-align: left;
}
.amazing-color{
  color:#9352ae;
  opacity: 1;
}

.contact .email{
  color:black;
  font-size: 2em;
}
.contact li{
  display: inline-block;
  margin-top:0;
  margin-bottom: 0;
  margin-right: 1em;
  margin-left: 0;
}
.contact li a{
  text-decoration: none;
  color:black;
}

.socials{
  margin: 5% 0% 0% 0%;
  padding: 5%;
}

/* SOCIAL STUFF */
.social-container {
  /* background: #eee; */
  padding: 0.5em;
}

a.social {
  margin: 0px 2.5rem 0px 0px;
  transition: transform 250ms;
  display: inline-block;
}

a.social:hover {
  transform: translateY(-15px);
}

a.github {
  color: #000000;
}

a.linkedin {
  color: #3076B0;
}

a.twitter {
  color: #49a1eb;
}

.footer{
  position: relative;
  top:2em;
  padding: 1em;
}
.footer a{
  text-decoration: none;
  color:#2c59fb;
}


/* SMARTPHONES PORTRAIT */
@media only screen and (min-device-width: 320px) and (max-device-width : 480px){

  .App-header {
    position: relative;
    min-height: 55vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black;
    top:4em;
  }

  body{
    font-size: 6px;
  }
  header h1{
    font-size: 5em;
    padding: 1em;
    margin-right: 0em;
  }

  .heading-background{
    display: none;
  }

  nav{
    display: none;
  }

  header h1{
    margin: 0em -1em 0.1em -4em;
    font-size: 2.5em 0;
    text-align: left;

  }

  .header-title{
    font-size: 2.5em;
    text-align: left;
    top:1em;
    left: -2em;
    margin: -2em;
  }
  .about{
    position: relative;
    display: flex;
    top:-30em;
    margin: 19px;
  }
  .about p{
    padding-bottom: 1.3em;
  }
  .about img{
    display: none
  }
  .about-content{
    left:2em;
  }
  .about-content p{
    font-size: 3.5em;
    text-align: left;
    width: 300px;
  }

  .work{
    left:2em;
    top:2em;
    align-items: flex-start;
    margin: 11vw;
  }
  .work .work-content{
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5em;
  }
  .project img{
    width: 45em;
    height: 45em;
  }

  .contact{
    top:15em;
    left:2em;
    margin: 11vw;
  }

  .contact h1{
    font-size: 4em;
  }

  .contact .email{
    font-size: 3.5em;
  }

  .contact li{
    display: block;
    padding: 2em;
  }

  .contact li a{
    font-size: 2.5em;
  }

  .footer{
    top:4em;
    padding: 1em;
    font-size: 2em;
  }


}

/* iPads (portrait and landscape) ----------- */
@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) {


  body{
      font-size: 10px;
    }
  header h1{
  font-size: 5em;
  padding: 1em;
  margin-right: 0em;
  }

  nav{
    display: none;
  }
  .header-title{
    font-size: 3.6em;
    text-align: initial;
    top: 3em;
    left: 1em;
  }

  .about{position: relative;
        display: flex;
        top:-80em;
  }

  .about img{
    display: none
  }

  .about-content{
    left:6em;
  }

  .about-content p{
    font-size: 3.5em;
    text-align: left;
    width: 95%;
  }

  .work{
    left:6em;
    top:4em;
    align-items: flex-start;
  }
  .work .work-content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 5em;
  }

  .project img{
  width: 30em;
  height: 30em;
  }

  .contact{
    top:15em;
    left:6em;
  }

  .contact h1{
    font-size: 4em;
  }

  .contact .email{
    font-size: 3.5em;
  }

  .socials{
    margin: 1% 0% 0% -5%;
  }

  .contact li{
    display: block;
    padding: 2em;
  }

  .contact li a{
    font-size: 2.5em;
  }

  .footer{
    top:2em;
    padding: 1em;
    font-size: 2em;
  }

}

/* Large screens ----------- */
@media only screen  and (min-width : 1824px) {
/* Styles */

  body{
      font-size: 20px;
    }
    header h1{
      font-size: 3em;
      margin-right: 25em;
    }

    nav{
    left:60em;
    top:12em;
    z-index: 100;
  }
  nav li{
     font-size: 3em;

  }
  .header-title{
    font-size: 5em;
    top:1em;
    left:1em;
  }
  .about{position: relative;
        display: flex;
        top:-20em;
  }
  .about-content{
    left:10em;
  }
  .about-content p{
    font-size: 3.5em;
    text-align: left;
    width: 25em;
  }
  .about img{
    margin-left: 1em;
  }

  .work{
    left:10em;
    top:4em;
    align-items: flex-start;
  }
  .work .work-content{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .project img{
  width: 32em;
  height: 32em;
  }

  .contact{
    top:10em;
    left:8em;
  }
  .contact h1{
    font-size: 8em;
  }
  .contact .email{
    font-size: 5em;
  }

  .contact li a{
    font-size: 4em;
    padding: 1em;
  }
  .footer{
    top:4em;
    padding: 1em;
    font-size: 4em;
  }


}
